export default {
	data() {
		return {
			title: 'Hello',
			name: "admin",
			userInfo: [],
			model: false
		}
	},
	onLoad() {
		let vm = this;
		uni.login({
			provider: 'weixin',
			success: function(loginRes) {
				// 获取用户信息
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
						console.log(infoRes.userInfo)
						vm.userInfo = infoRes.userInfo
						vm.model = true;
					}
				});
			}
		});
	},
	methods: {

	}
}
